// --------------------------------------------
// This file contains form styles
// --------------------------------------------
@import "sass/partials/abstracts/mixins";
@import "sass/partials/abstracts/variables";

form {
  width: 500px;
  padding: 2rem;
  label {
    justify-content: flex-start;
    text-align: left;
    width: 85%;
  }
}

input {
  display: block;
  outline: none;
  padding: 1rem;
  width: 100%;
  border: var(--border);
  border-radius: var(--border-radius);
}
